.menu {
  width: 115px;
  height: 100vh;
  background: #625dee;
  border-radius: 0px 48px 0px 0px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.menu__logo {
  position: absolute;
  top: 27px;
  left: 50%;
  transform: translate(-50%, 0%);
}

.menu__logo img {
  width: 60px;
  height: 60px;
  transition: all 0.5s ease;
  transform-origin: center;
  transform-box: fill-box;
  animation: rotate 3s linear infinite;
}

.menu__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin: auto 0;
  padding: 50% 0 30px 0;
  overflow: hidden;
}

.menu__list-items {
  position: relative;
  margin: 0 0 15px 22px;
  background: transparent;
  height: 60px;
  width: 115px;
  padding: 0 0 0 25px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
}

.menu__list-items:last-child {
  margin: 0 0 0 22px;
}

.menu__list-items svg {
  width: 25px;
  height: 25px;
}

.menu__list-items .menu-icons {
  fill: #fff;
}

.menu__list-items-active {
  background: #f5f7fb;
  border-radius: 32px 0px 0px 32px;
}

.menu__list-items-active::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -22px;
  background: #625dee;
  border-radius: 0 0 30px 0;
  width: 100%;
  height: 30px;
  z-index: 5;
}

.menu__list-items-active::after {
  content: "";
  position: absolute;
  bottom: -50%;
  left: -22px;
  background: #625dee;
  border-radius: 0 30px 0 0;
  width: 100%;
  height: 30px;
  z-index: 5;
}

.menu__list-items-active .menu-icons {
  fill: #625dee;
}

.menu__list-items-active .menu__list-items-add {
  position: absolute;
  top: -50%;
  right: 0;
  width: 60px;
  height: 120px;
  background: #f5f7fb;
}

.t-header__nav {
  padding: 45px 0 30px 115px;
  font-family: Inter;
}

.t-header__nav .wrappers {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.t-header__nav-title {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #435179;
}

.t-header__nav-title p {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #435179;
}

.t-header__nav-client {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.t-header__nav-client-sum {
  display: flex;
  cursor: pointer;
  align-items: center;
  justify-content: flex-start;
}

.t-header__nav-client-sum h3 {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #625dee;
}

.t-header__nav-client-sum p {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #625dee;
  margin: 0 0 0 6px;
}

.t-header__nav-client-notif {
  width: 20px;
  height: 26px;
  cursor: pointer;
  position: relative;
  margin: 0 23px 0 63px;
}

.t-header__nav-client-notif::after {
  content: "";
  position: absolute;
  top: -2px;
  right: -2px;
  background: #5447c8;
  width: 11.5px;
  height: 11.5px;
  border-radius: 50%;
}

.t-header__nav-client-notif svg {
  width: 20px;
  height: 26px;
}

.t-header__nav-client-profile {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  position: relative;
}

.t-header__nav-client-profile-photo {
  background: #c5cee0;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
}

.t-header__nav-client-profile-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.t-header__nav-client-profile-name {
  margin: 0 0 0 11.5px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.t-header__nav-client-profile-name h3 {
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  color: #222b45;
}

.t-header__nav-client-profile-name p {
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  color: #7b91b0;
}

.t-header__nav-client-arrow {
  margin: 0 0 0 25px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 10px;
  transition: all 0.5s ease;
}

.t-header__nav-client-list {
  position: absolute;
  bottom: calc(-100% - 5px);
  right: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #fff;
  justify-content: flex-start;
  border-radius: 15px 15px 0 0;
}

.t-header__nav-client-btn {
  width: 100%;
  background: #fff;
  padding: 12px 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #edf2f6;
}

.t-header__nav-client-btn:first-child {
  border-radius: 10px 10px 0 0;
}

.t-header__nav-client-btn:last-child {
  border-radius: 0 0 10px 10px;
  border-bottom: none;
}

.t-header__nav-client-btn p {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #222b45;
}

