.s-list__title {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #435179;
}

.s-list__nav {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 52px 0 0 0;
}

.s-list__nav-items p {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #435179;
}

.s-list__nav-items:nth-child(1) {
  margin: 0 0 0 17px;
}

.s-list__nav-items:nth-child(2) {
  margin: 0 0 0 335px;
}

.s-list__nav-items:nth-child(3) {
  margin: 0 0 0 178px;
}

.s-list__block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.s-list__block-items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 23px 0 0 0;
}

.s-list__block-items:last-child {
  margin: 23px 0;
}

.s-list__block-items-info {
  background: #FFFFFF;
  padding: 13px 18px;
  max-width: 880px;
  width: 100%;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.s-list__block-items-info-photo {
  width: 36px;
  height: 36px;
  background: #635FF2;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: hidden;
}

.s-list__block-items-info-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.s-list__block-items-info-name {
  margin: 0 0 0 16px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #435179;
  width: 340px;
}

.s-list__block-items-info-lesson {
  margin: 0 0 0 auto;
  width: 170px;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  line-height: 12px;
  color: #8349FF;
}

.s-list__block-items-info-date {
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  line-height: 12px;
  width: 130px;
  margin: 0 0 0 auto;
  color: #435179;
}

.s-list__block-items-btn {
  cursor: pointer;
  background: #625DEE;
  border-radius: 10px;
  padding: 16px 55px;
}

.s-list__block-items-btn p {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #FFFFFF;
}

.s-pay {
  padding: 65px 30px 70px 115px;
  font-family: Inter;
}

.s-pay .wrappers {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.s-pay__status-ready {
  color: #5EC3AC !important;
}

.s-pay__status-ready::after {
  background: #5EC3AC !important;
}

.s-pay__status-cancel {
  color: #FF2D55 !important;
}

.s-pay__status-cancel::after {
  background: #FF2D55 !important;
}

.s-pay__history {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 790px;
  width: 100%;
}

.s-pay__history-title {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #435179;
}

.s-pay__history-nav {
  margin: 52px 0 0 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.s-pay__history-nav-items p {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #435179;
}

.s-pay__history-nav-items:nth-child(1) {
  margin: 0 0 0 22px;
}

.s-pay__history-nav-items:nth-child(2) {
  margin: 0 0 0 138px;
}

.s-pay__history-nav-items:nth-child(3) {
  margin: 0 0 0 53px;
}

.s-pay__history-nav-items:nth-child(4) {
  margin: 0 0 0 115px;
}

.s-pay__history-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
}

.s-pay__history-list-items {
  margin: 23px 0 0 0;
  background: #FFFFFF;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 23px;
  max-width: 790px;
  width: 100%;
}

.s-pay__history-list-items:last-child {
  margin: 23px 0;
}

.s-pay__history-list-items-date {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #435179;
  width: 110px;
}

.s-pay__history-list-items-price {
  margin: 0 0 0 auto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
  width: 100px;
}

.s-pay__history-list-items-status {
  padding: 0 0 0 21px;
  width: 100px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  color: #000;
  margin: 0 0 0 auto;
  position: relative;
}

.s-pay__history-list-items-status::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0%, -50%);
  border-radius: 50%;
  width: 6px;
  height: 6px;
  background: #000;
}

.s-pay__history-list-items-comment {
  margin: 0 0 0 auto;
  width: 210px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #435179;
}

.s-pay__account {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  max-width: 372px;
}

.s-pay__account-title {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #435179;
}

.s-pay__account-add {
  margin: 52px 0 0 28px;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #435179;
}

.s-pay__account-block {
  background: #FFFFFF;
  border-radius: 12px;
  padding: 29px 41px;
  width: 100%;
  margin: 22px 0 0 0;
}

.s-pay__account-block-sum {
  width: 290px;
  background: #625DEE;
  border-radius: 9px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 20px;
  position: relative;
  overflow: hidden;
}

.s-pay__account-block-sum-add {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  color: #8D9FFF;
}

.s-pay__account-block-sum-items {
  margin: 22px 0 0 0;
  font-style: normal;
  font-weight: bold;
  font-size: 42px;
  line-height: 24px;
  color: #FFFFFF;
  width: 180px;
}

.s-pay__account-block-sum-text {
  margin: 58px 0 0 0;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: #8D9FFF;
  width: 200px;
}

.s-pay__account-block-sum-circle {
  position: absolute;
  right: 20px;
  bottom: 20px;
}

.s-pay__account-block-sum-circle::after {
  content: "";
  position: absolute;
  right: 0px;
  bottom: 0px;
  width: 16px;
  height: 16px;
  background: #5A55D6;
  border-radius: 50%;
  z-index: 1;
}

.s-pay__account-block-sum-circle::before {
  content: "";
  position: absolute;
  right: 8px;
  bottom: 0px;
  width: 16px;
  height: 16px;
  background: #FF5283;
  border-radius: 50%;
  z-index: 3;
}

.s-pay__account-block-sum::after {
  content: "";
  position: absolute;
  width: 87px;
  height: 87px;
  right: 7px;
  z-index: 1;
  top: -40px;
  border-radius: 50%;
  background: #FF5283;
}

.s-pay__account-block-sum::before {
  content: "";
  position: absolute;
  width: 87px;
  height: 87px;
  right: -36px;
  top: -1px;
  z-index: 3;
  border-radius: 50%;
  background: #5A55D6;
}

.s-pay__account-block-input {
  margin: 28px 0 0 0;
  background: #FFFFFF;
  border: 2px solid #625DEE;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 18px 34px;
  width: 100%;
  border-radius: 26.5px;
}

.s-pay__account-block-btn {
  margin: 28px 0 0 0;
  background: #FF5283;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px 50px;
  width: 100%;
}

.s-pay__account-block-btn p {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #FFFFFF;
}