.t-profile__banners {
	padding: 0 0 30px 115px;
	font-family: Inter;
	& .wrappers {
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(390px, 1fr));
		gap: 34px;
	}
	&-items {
		padding: 9px 30px;
		width: 100%;
		border-radius: 20px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		&:nth-child(1) {
			background: #fff;
			& .t-profile__banners-text {
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				justify-content: flex-start;
				& h3 {
					font-style: normal;
					font-weight: bold;
					font-size: 42px;
					line-height: 51px;
					color: #435179;
				}
				& p {
					margin: 3px 0 0 0;
					font-style: normal;
					font-weight: normal;
					font-size: 16px;
					line-height: 19px;
					color: #747c93;
				}
			}
		}
		&:nth-child(2) {
			background: #625dee;
			& .t-profile__banners-text {
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				justify-content: flex-start;
				& h3 {
					font-style: normal;
					font-weight: bold;
					font-size: 42px;
					line-height: 51px;
					color: #fff;
				}
				& p {
					margin: 3px 0 0 0;
					font-style: normal;
					font-weight: normal;
					font-size: 16px;
					line-height: 19px;
					color: #fff;
				}
			}
		}
		&:nth-child(3) {
			background: #ff5283;
			& .t-profile__banners-text {
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				justify-content: flex-start;
				& h3 {
					font-style: normal;
					font-weight: bold;
					font-size: 42px;
					line-height: 51px;
					color: #fff;
				}
				& p {
					margin: 3px 0 0 0;
					font-style: normal;
					font-weight: normal;
					font-size: 16px;
					line-height: 19px;
					color: #fff;
				}
			}
		}
	}
	&-icons {
		width: 106px;
		height: 100px;
		overflow: hidden;
	}
}

